.card {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
  }
  .container {
    position: relative;
    max-width: 360px !important;
    min-width: 360px;
    margin: 2em auto;
  }
  .image {
    display: block;
    width: 100%;
    height: auto;
  }
  .image-Kitchen{
      display: block;
      width: 100%;
      height: 100%;
  }
  .overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    transition: 0.5s ease;
    background-color: #f7a440;
  }
  
  .container:hover .overlay {
    opacity: 1;
  }
  
  .text {
    color: white;
    font-size: 20px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
  }
  .container a {
    text-decoration: none;
    font-size: 50px;
    color: white;
  }
  ::-webkit-scrollbar{
    width: 3px;
  }  
  
  ::-webkit-scrollbar-track{
    background: initial;
  }
  
  ::-webkit-scrollbar-thumb{
    background-color: #f7a440;
    border-radius: 50px;
  }
  